import Axios from 'axios';

var my_axios = {};

my_axios.install = function(Vue){

    var obj_axios = Axios.create({
        baseURL: "http://www.luoying.com/works/2019.02.28/api/index.php",
        method: "post",
    });

    Vue.prototype.$axios = obj_axios;
}

export default my_axios;


